.host {
  position: relative;
  height: 100vh;

  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: stretch;
  flex: auto;

  @media (--app-viewport-xsmall) {
    flex-direction: column;
  }

  @media (--app-viewport-small) {
    flex-direction: column;
  }

  @media (--app-viewport-medium) {
    flex-direction: row;
  }

  @media (--app-viewport-large) {
    flex-direction: row;
  }

  &[app-custom-media-show="show"] {
    @media (--app-viewport-xsmall) {
      background-color: var(--error);
    }

    @media (--app-viewport-small) {
      background-color: var(--success);
    }

    @media (--app-viewport-medium) {
      background-color: var(--attention);
    }

    @media (--app-viewport-large) {
      background-color: var(--notice);
    }
  }
}

.firstContainer {
  align-self: flex-start;
  display: flex;
  flex: auto;
  flex-direction: column;
  background-color: white;
  height: 100%;
}

.headerTopBarContainer {
