#reset-password-container {
  .second-container-content {
    margin-left: 70px;
  }

  .left-20 {
    padding-left: 15%;
  }

  .logo-container {
    display: flex;
    padding-top: 30px;
  }

  .logo-size {
    width: 25px;
    height: 25px;
  }

  .main-form {
    position: relative;
    top: 20vh;
  }

  .message-container {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    font-size: 1em;
    margin-top: 30px;
    border-radius: 3px;
    color: #721c24;
    background-color: #f8d7da;
    border-color: #f5c6cb;
    padding: 2px;
  }

  .input-style {
    font-family: var(--app-text-main-font), serif !important;
    font-size: 16px !important;
    line-height: 20px !important;
    color: var(--app-color-gray-dark) !important;
    width: 300px;
  }

  .login-btn {
    height: 50px;
    width: 100px;
    border-radius: 8px;
    border: 0;
    background-color: var(--app-color-gray-dark);
  }

  .btn-primary:hover {
    background-color: #333333;
  }

  input[type="text"] {
    padding: 20px;
  }

  input[type="password"] {
    padding: 20px;
  }

  .logo-text-style {
    font-family: var(--app-text-main-font), serif;
    font-size: 24px;
    line-height: 24px;
    text-align: left;
    color: var(--app-color-gray-dark);
  }

  .login-text-style {
    font-family: var(--app-text-main-font);
    font-size: 32px;
    line-height: 40px;
    text-align: left;
    color: var(--app-color-gray-dark);
  }

  .alert {
    width: 300px !important;
    margin-top: 30px;
  }

  .terms-and-cond-text-style {
    font-family: var(--app-text-main-font), serif;
    font-size: 16px;
    line-height: 22px;
    color: var(--app-color-gray-light);
  }

  .remember-me {
    font-family: var(--app-text-main-font), serif;
    font-size: 16px;
    line-height: 22px;
    color: #333333;
    padding-left: 10px;
    /* padding-top: 2px; */
    min-width: 24px;
    min-height: 24px;
  }

  .bold {
    color: var(--app-color-gray-dark);
  }

  .login-btn span {
    font-family: var(--app-text-main-font), serif;
    font-size: 16px;
    line-height: 20px;
    color: #ffffff;
  }

  @media (max-width: 836px) {
    .main-container {
      background-color: var(--app-color-white-lightest);
    }

    .need-help-main-container {
      background-color: #f1f1f1 !important;
    }

    .left-20 {
      padding-left: 2%;
    }
  }
}